.movie {
    background-color: white;
    margin-top: 70px;
    font-weight: 300px;
    padding: 20px;
    border-radius: 5px;
    color: #adaeb9;
    display: flex;
    text-decoration: none;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie__img {
    position: relative;
    top: -50px;
    max-width: 180px;
    margin-right: 30px;
    width: 100%;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}


.movie__img {
    position: relative;
    top: -50px;
    max-width: 180px;
    margin-right: 30px;
    width: 100%;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie__title,
.movie__year {
    margin: 0;
    font-weight: 300;
    text-decoration: none;
}

.movie__title {
    margin-bottom: 5px;
    font-size: 24px;
    color: #2c2c2c;
    text-decoration: none;
}

.movie__genres {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    margin: 5px 0px;
}

.movie__genres li,
.movie__year {
    margin-right: 10px;
    font-size: 14px;
}

.ratingStar {
    color: #FFC700;
    margin-right: 3px;
}

.btnGoSite {
    position: fixed;
    margin: 5px auto;
    padding: 15px 30px;
    font-family: "paybooc-Light", sans-serif;
    box-shadow: 0 1px 20px rgba(0, 0, 0, 0.2);
    text-decoration: none;
    background-color: white;
    border-radius: 15px;
    font-weight: 600;
    color: steelblue;
}

.btnGoSite:hover {
    background-color: steelblue;
    color: white;
}